<template>
    <div id="app"
         v-bind:class="{
            login: $route.path === '/login' || $route.name === 'not_found'
      }"
    >
        <Sidebar  v-show="$route.path !== '/login' && $route.name !== 'not_found'"></Sidebar>
        <router-view/>

        <transition name="fade">
            <div class="ui active inverted dimmer" v-show="busy === true">
                <div class="ui text loader"></div>
            </div>
        </transition>
    </div>
</template>


<script>
    import axios from 'axios'

    import Sidebar from './layouts/Sidebar'
    import Snackbar from 'node-snackbar';

    export default {
        name: 'app',
        components: {
            Sidebar
        },
        data() {
            return {
                busy: false,
                meta_loaded: false,
                user: {
                    token: null
                },
                // 갯수 목록
                page_sizes: [
                    {
                        text: '100개씩 보기',
                        value: 100,
                    },
                    {
                        text: '50개씩 보기',
                        value: 50,
                    },
                    {
                        text: '25개씩 보기',
                        value: 25,
                    },
                    {
                        text: '10개씩 보기',
                        value: 10,
                    },
                    {
                        text: '1개씩 보기',
                        value: 1,
                    }
                ]
            }
        },
        mounted() {
            const self = this;
        },
        methods: {
            showSuccess(msg, callback, showAction = false, onActionCallback = function() {return false;}) {
                // Call callback function
                if(callback) callback();

                Snackbar.show({
                    text: msg,
                    customClass: 'success',
                    showAction: showAction,
                    pos: 'bottom-center',
                    onActionClick: onActionCallback,
                    duration: 3000
                });
            },
            showError(e, callback) {

                // Call callback function
                if(callback) callback();

                // Show snackbar error message
                Snackbar.show({
                    text: (e.response !== undefined && e.response.data.message ? e.config.method.toUpperCase() + ': ' +e.config.url + '<br/><br/>' + e.response.data.message : '오류가 발생했습니다.'),
                    customClass: 'error',
                    showAction: false,
                    actionText: '<i class="window close icon"></i>',
                    pos: 'bottom-center',
                    duration: 3000
                });
            },
        }
    }
</script>


