<template>
    <div class="field" v-bind:class="{ required: props.option && props.option.required }">
        <label>{{ props.label }}</label>
        <select class="ui search dropdown"
                :name="'field'+props.sn"
                :id="'field'+props.sn+'_'+index"
                :data-key="props.key"
                data-select="container"
                :data-index="index"
                v-bind:multiple="props.option.multiple !== undefined && props.option.multiple === true"
                v-model="value"
                @change="getOptions"
        >
            <option value="">{{ props.placeholder }}</option>
        </select>
        <p class="postscript" v-if="props.postscript">{{ props.postscript }}</p>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: 'SelectContainer',
        props: [
            'props',
            'index'
        ],
        data: function () {
            return {
                type: false,
                timeout: null,
                options: [],
                value: null
            }
        },
        mounted() {
            const self = this;

            const autocomplete = process.env.VUE_APP_API_HOST + "/sbculture/rest/memex/container/autocomplete/";

            $('#field'+self.props.sn+'_'+self.index).dropdown({
    apiSettings: {
      // this url parses query server side and returns filtered results
      url: process.env.VUE_APP_API_HOST + "/sbculture/rest/memex/container/autocomplete/" + parseInt(self.props.option.reference) + '?name={query}',
        method: 'post',
        onResponse: function(res) {
      // always called after XHR complete
            console.log(res)

            var response = {
                success: true,
                results: []
            };

            $.each(res.values, function(index, item) {
                response.results.push({
                    name: item.name,
                    text: item.name,
                    value: item.sn
                })
            });


            console.log(response);


            return response;
    },
    },
  });
        },
        methods: {
            getOptions() {
                let self = this;

                if (this.timeout !== null) {
                    clearTimeout(this.timeout);
                }
                this.timeout = setTimeout(function () {
                    this.timeout = null;
                    if (self.input) {
                        // Call API
                        axios.post(process.env.VUE_APP_API_HOST + "/sbculture/rest/memex/container/autocomplete/" + self.$route.params.sn)
                            .then(res => {
                                self.options = res.data.saves;
                            })
                            .catch(e => {
                                self.options = [];
                            });
                    } else {
                        self.options = [];
                    }
                }, 300);
            }
        }
    }
</script>