<template>
    <div :class="getGrid(parseInt(props.option.depth)) + ' fields'">
        <div class="field"
             v-bind:class="{ required: props.option && props.option.required }"
             v-for="(component, depth) in parseInt(props.option.depth)"
        >
            <label>{{ props.label }}</label>
            <select class="ui search tree dropdown"
                    :name="'field'+props.sn+'_'+(depth + 1)"
                    :id="'field'+props.sn+'_'+index+'_'+(depth + 1)"
                    v-model="data[depth]"
                    @change="resetChild(depth)"
                    :data-key="props.key"
                    data-select="tree"
                    :data-sn="props.sn"
                    :data-depth="depth + 1"
            >
                <option value="-1">{{ props.placeholder }}</option>
                <option :value="code.sn" v-if="depth === 0" v-for="(code, codeIndex) in props.codes">{{ code.name }}</option>
                <option :value="opt.sn" v-if="opt.depth !== 0 && data[depth - 1] && opt.parentSn === data[depth - 1]" v-for="opt in opts">{{ opt.name }}</option>
            </select>
            <p class="postscript" v-if="props.postscript">{{ props.postscript }}</p>
            <input
                :name="'field'+props.sn+'_'+(depth + 1)"
                v-model="data"
                style="opacity: 0; visibility: hidden;height: 0; margin0; padding: 0;"
            />
        </div>
            <input
                :name="'field'+props.sn"
                v-model="data"
                style="opacity: 0; visibility: hidden;height: 0; margin0; padding: 0;"
            />
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: 'SelectTree',
        props: [
            'props',
            'index',
            'grid'
        ],
        data: function () {
            return {
                count: 0,
                data: [],
                opts: []
            }
        },
        mounted() {
            const self = this;

            for(let i = 0; i < self.props.option.depth; i++) {
                self.data.push(-1);

                self.$bus.$on('change_tree_'+self.props.sn+'_'+i, function (val) {
                    self.data[i] = parseInt(val);
                    setTimeout(() => {
                        $('.ui.dropdown.tree').dropdown();
                        self.$forceUpdate();
                    });
                });
            }

            self.getCodes(self.props.codes, 0, null);

            $('.ui.dropdown.tree').dropdown();
        },
        methods: {
            getCodes(codes, depth, parentSn) {
                const self = this;

                if(codes === undefined) return;

                for(let i = 0; i < codes.length; i++) {
                    codes[i].depth = depth;

                    if(depth && parentSn !== undefined) {
                        codes[i].parentSn = parentSn;
                        self.opts.push(codes[i]);
                    }

                    if(codes[i].codes) {
                        self.getCodes(codes[i].codes, depth + 1, codes[i].sn);
                    }
                }
            },
            resetChild(depth) {
                const self = this;

                if(self.data[depth + 1] !== undefined) self.data[depth + 1] = -1;
            }
        }
    }
</script>