<template>
    <div class="field" v-bind:class="{ required: props.option && props.option.required }">
        <label>{{ props.label }}<span v-show="props.option.multiple"> (최대 {{ props.option.max }}개)</span></label>
        <div class="ui input">
            <!--<div :id="'field'+props.sn+'_'+index" class="dropzone"></div>-->
            <form
                    method="post"
                    class="dropzone"
                    enctype="multipart/form-data"
                    :action="'/sbculture/rest/file/upload'"
                    :id="'field'+props.sn+'_'+index"
            >
            </form>
        </div>
        <p class="postscript" v-if="props.postscript">{{ props.postscript }}</p>
    </div>
</template>

<script>
    import axios from 'axios'
    import Dropzone from 'dropzone'

    Dropzone.autoDiscover = false;

    export default {
        name: 'File',
        props: [
            'props',
            'index',
            'sn'
        ],
        data: function () {
            return {
                count: 0,
                data: null,
                fileDropzone: null
            }
        },
        watch: {
            '$route.params.id': function (id) {
                // Remove old file array
                console.log("Route change");
                $('.file-array').remove();
            }
        },
        mounted() {
            const self = this;

            self.fileDropzone = new Dropzone('#field'+self.props.sn+'_'+self.index, {
                url: process.env.VUE_APP_API_HOST + '/sbculture/rest/file/upload',
                method: "POST",
                paramName: "uploadFile",
                maxFiles: self.props.option.max,
                addRemoveLinks: true,
                dictDefaultMessage: '파일을 업로드 하려면 <strong>드래그 앤 드랍(Drop and Drop)</strong>으로 올려놓거나 <strong>클릭</strong>하세요.'+(self.props.option.max > 1 ? '<br/><br/>여러개의 파일을 올릴 수 있습니다.' : ''),
                dictCancelUpload: '취소',
                dictCancelUploadConfirmation: '업로드를 취소할까요?',
                dictRemoveFile: '삭제',
                maxfilesexceeded: function(file) {
                    alert("최대 "+self.props.option.max+"개의 파일만 가능합니다.");
                    this.removeFile(file);
                },
                init: function() {
                    this.on("sending", function(file, xhr, formData) {
                        formData.append('containerSn', parseInt(self.sn));

                        for (let key in self.props.option.thumb) {
                            formData.append('thumb-type', self.props.option.thumb[key].type);
                            formData.append('thumb-width', self.props.option.thumb[key].width);
                            formData.append('thumb-height', self.props.option.thumb[key].height);
                        }
                    });
                    this.on("success", function(file, data) {
                        // Add file number
                        file.id = data.uploadFile.sn;

                        self.addFileArray(file.id);

                        // File Download Click
                        file.previewElement.onclick = function() {
                            window.open(process.env.VUE_APP_API_HOST + '/sbculture/page/file/original/view/'+file.id);
                        };
                    }),
                    this.on("removedfile", function(file) {
                        $("#file"+self.props.sn+'_'+self.index+'_'+file.id).remove();
                    });
                },
            });


            // Event on: Remove address
            self.$bus.$on('add_file', function (sn) {
                self.addFile(sn, process.env.VUE_APP_API_HOST + '/sbculture/page/file/original/view/'+sn);
            });
        },
        beforeDestroy(){
            self.$bus.$off('add_file');
      },
        methods: {
            addFileArray(value) {
                const self = this;

                if(!$('#file'+self.props.sn+'_'+self.index+'_'+value).length) {
                    $("<input>", {
                        type: "hidden",
                        name: "field"+self.props.sn,
                        id: 'file'+self.props.sn+'_'+self.index+'_'+value,
                        class: 'file-array file'+self.props.sn+'_'+self.index,
                        value: value
                    }).appendTo($('#record-form'));
                }
            },
            addFile(id, path) {
                const self = this;
                let mockFile = { id: id, name: id, size: 0, accepted: true };

                self.fileDropzone.emit("addedfile", mockFile);
                self.fileDropzone.emit("thumbnail", mockFile, path);

                self.addFileArray(id);

                //self.fileDropzone.createThumbnailFromUrl(mockFile, path);
                self.fileDropzone.emit("complete", mockFile);

                // File Download Click
                mockFile.previewElement.onclick = function() {
                    window.open(path);
                };

                self.fileDropzone.files.push(mockFile);
            }
        }
    }
</script>