<template>
    <div :class="'ui set set-sort '+'set-sort-'+props.sn" :id="'set-sort-'+props.sn">
        <ol class="set-sort-list ui segments">
            <li class="ui segment set-sort-item"
                v-for="(set, index) in sets"
                :id="'set-sort-item-'+props.sn+'-'+index"
            >
                <div class="set-sort-handle" v-show="props.option.multiple === true">Drag</div>
                <div class="set-sort-content">
                    <div class="ui field">
                        <div class="ui grid" style="margin-bottom: 0;">
                            <div :class="component.depth ? getGrid(component.grid * parseInt(component.depth)) + ' wide column' : getGrid(component.grid) + ' wide column'"
                                v-for="(component, compIndex) in set.components"
                            >
                                <Header
                                    v-if="component.definition === 'header'"
                                    v-bind:props="component"
                                    v-bind:index="index"
                                ></Header>
                                <Input
                                    v-if="component.definition === 'input'"
                                    v-bind:props="component"
                                    v-bind:data="component.data"
                                    v-bind:index="index"
                                ></Input>
                                <Textarea
                                    v-if="component.definition === 'textarea'"
                                    v-bind:props="component"
                                    v-bind:data="component.data"
                                    v-bind:index="index"
                                ></Textarea>
                                <Checkbox
                                    v-if="component.definition === 'checkbox'"
                                    v-bind:props="component"
                                    v-bind:data="component.data"
                                    v-bind:index="index"
                                ></Checkbox>
                                <RadioCode
                                    v-if="component.definition === 'radio' && component.type === 'code'"
                                    v-bind:props="component"
                                    v-bind:data="component.data"
                                    v-bind:index="index"
                                ></RadioCode>
                                <RadioNumber
                                    v-if="component.definition === 'radio' && component.type === 'number'"
                                    v-bind:props="component"
                                    v-bind:data="component.data"
                                    v-bind:index="index"
                                ></RadioNumber>
                                <SelectCode
                                    v-if="component.definition === 'select' && component.type === 'code'"
                                    v-bind:props="component"
                                    v-bind:data="component.data"
                                    v-bind:index="index"
                                ></SelectCode>
                                <SelectTree
                                    v-if="component.definition === 'select' && component.type === 'tree'"
                                    v-bind:props="component"
                                    v-bind:data="component.data"
                                    v-bind:index="index"
                                ></SelectTree>
                                <SelectContainer
                                    v-if="component.definition === 'select' && component.type === 'container'"
                                    v-bind:props="component"
                                    v-bind:data="component.data"
                                    v-bind:index="index"
                                ></SelectContainer>
                            </div>
                        </div>
                    </div>
                    <button class="ui icon negative tiny button button-minus" type="button" v-show="props.option.multiple === true" v-on:click="removeSet(index)">
                        <i class="minus icon"></i>
                    </button>
                </div>
            </li>
        </ol>
        <button class="positive ui right floated labeled icon button button-add" type="button" v-show="props.option.multiple === true" v-on:click="addSet"><i class="plus icon"></i> 추가</button>
    </div>
</template>

<script>
    import axios from 'axios'
    import RadioCode from './RadioCode'
    import RadioNumber from './RadioNumber'
    import Checkbox from '../components/Checkbox'
    import SelectCode from './SelectCode'
    import SelectTree from '../components/SelectTree'
    import SelectContainer from '../components/SelectContainer'
    import Input from '../components/Input'
    import Textarea from '../components/Textarea'
    import Header from '../components/Header'

    export default {
        name: 'Set',
        components: {
            RadioCode,
            RadioNumber,
            Checkbox,
            SelectCode,
            SelectTree,
            SelectContainer,
            Input,
            Textarea,
            Header
        },
        props: [
            'props'
        ],
        data: function () {
            return {
                sets: [],
                components: []
            }
        },
        mounted() {
            const self = this;

            self.sets = [];

            self.sets.push({
                'idx': 0,
                'components': self.props.components
            });

            self.enableSort();

            // Event on: Add set
            self.$bus.$on('add_set', function () {
                self.addSet();
            });

            // Event on: Remove set
            self.$bus.$on('remove_set', function (index) {
                self.removeSet(index);
            });
        },
        methods: {
            enableSort() {
                const self = this;

                $('#set-sort-'+self.props.sn).nestable({
                    maxDepth: 1,
                    rootClass: 'set-sort-'+self.props.sn,
                    listClass: 'set-sort-list',
                    itemClass: 'set-sort-item',
                    dragClass: 'set-sort-dragel',
                    handleClass: 'set-sort-handle',
                    collapsedClass: 'set-sort-collapsed',
                    placeClass: 'set-sort-placeholder',
                    emptyClass: 'set-sort-empty'
                });
            },
            addSet() {
                const self = this;

                self.sets.push({
                    'idx': 0,
                    'components': self.props.components
                });
            },
            removeSet(index) {
                const self = this;

                const _ele = document.getElementById('set-sort-item-'+self.props.sn+'-'+index);
                _ele.parentNode.removeChild(_ele);
            }
        }
    }
</script>