<template>
    <div class="ui content">


        <!-- Tool -->
        <div class="ui toolbar">
            <h1 id="toolbar-title" v-show="name">성북구의 {{ name }}</h1>
            <div class="toolbar-btns">
                <router-link :to="{ name: 'records.create', params: { id: $route.params.id }}"
                             class="primary ui button">자료 등록
                </router-link>
            </div>
        </div><!-- //Tool -->


        <!-- Search -->
        <div class="ui search-segment segments">
            <form class="ui form" v-on:submit.prevent="search">
                <div class="ui top attached segment">
                    <div class="ui grid-search grid">
                        <div :class="component.option && component.option.depth ? getGrid(component.grid * parseInt(component.option.depth)) + ' wide column' : getGrid(component.grid) + ' wide column'"
                             v-for="(component, index) in searchComponents"
                        >
                            <Header
                            v-if="component.definition === 'header'"
                            v-bind:props="component"
                            v-bind:index="0"
                    ></Header>
                    <Input
                            v-if="component.definition === 'input'"
                            v-bind:props="component"
                            v-bind:data="component.data"
                            v-bind:index="0"
                    ></Input>
                    <Textarea
                            v-if="component.definition === 'textarea'"
                            v-bind:props="component"
                            v-bind:data="component.data"
                            v-bind:index="0"
                    ></Textarea>
                    <Checkbox
                            v-if="component.definition === 'checkbox'"
                            v-bind:props="component"
                            v-bind:data="component.data"
                            v-bind:index="0"
                    ></Checkbox>
                    <RadioCode
                            v-if="component.definition === 'radio' && component.type === 'code'"
                            v-bind:props="component"
                            v-bind:data="component.data"
                            v-bind:index="0"
                    ></RadioCode>
                    <RadioNumber
                            v-if="component.definition === 'radio' && component.type === 'number'"
                            v-bind:props="component"
                            v-bind:data="component.data"
                            v-bind:index="0"
                    ></RadioNumber>
                    <SelectCode
                            v-if="component.definition === 'select' && component.type === 'code'"
                            v-bind:props="component"
                            v-bind:data="component.data"
                            v-bind:index="0"
                    ></SelectCode>
                    <SelectTree
                            v-if="component.definition === 'select' && component.type === 'tree'"
                            v-bind:props="component"
                            v-bind:data="component.data"
                            v-bind:index="0"
                    ></SelectTree>
                    <SelectContainer
                            v-if="component.definition === 'select' && component.type === 'container'"
                            v-bind:props="component"
                            v-bind:data="component.data"
                            v-bind:index="0"
                    ></SelectContainer>
                    <Set
                            v-if="component.definition === 'set'"
                            v-bind:props="component"
                    ></Set>
                        </div>
                    </div>
                </div>
                <div class="ui bottom attached right aligned segment">
                    <div class="ui buttons">
                        <button class="ui right labeled icon button" type="button" @click="reset"><i class="refresh icon"></i> 초기화
                        </button>
                        <button class="ui right labeled icon positive button"><i class="search icon"></i> 검색</button>
                    </div>
                </div>
            </form>
        </div><!-- //Search -->


        <!-- Tool -->
        <div class="ui grid">
            <div class="twelve wide column">
                <div class="ui buttons" style="margin-right: 1rem;">
                    <button class="ui right labeled icon positive button" @click="downloadAllExcel">
                        <i class="right download icon"></i>
                        엑셀 전체 다운로드
                    </button>
                    <button class="ui right labeled icon positive button" @click="downloadExcel">
                        <i class="right download icon"></i>
                        엑셀 선택 다운로드
                    </button>
                </div>
                <button class="ui button" style="margin-right: 1rem;">삭제</button>
                <!--<button class="ui button">일괄수정</button>-->
            </div>
            <div class="four wide column text-right">
                <select class="ui search dropdown" name="page_size" v-model="size" @change="page = 1; getData($route.params.id)">
                    <option :value="type.value" v-for="(type, index) in $parent.page_sizes">{{ type.text }}</option>
                </select>
            </div>
        </div>
        <!-- //Tool -->


        <!-- Data List -->
        <table class="ui celled table form">
            <colgroup>
                <col width="30px"/>
                <col width="60px"/>
                <col v-for="(component, index) in listComponents"/>
                <col width="120px"/>
                <col width="120px"/>
                <col width="120px"/>
            </colgroup>
            <thead>
            <tr>
                <th>
                    <sui-checkbox label="" v-model="checked_all" @change="toggleCheckAll()"/>
                </th>
                <th>번호</th>
                <th v-for="(component, index) in listComponents">
                    {{ component.label }}
                </th>
                <th>등록일</th>
                <th>수정일</th>
                <th>관리</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(item, index) in list">
                <td>
                    <sui-checkbox label="" v-model="item.is_checked" @change="toggleCheck(item)"/>
                </td>
                <td>{{ paging.numberOfPages - index }}</td>
                <td v-for="(component, compIndex) in listComponents">
                    {{
                        component.key === 'confirm' ?
                            component.codes && component.codes.find(x => x.sn === parseInt(item[component.key])).name :
                            (
                                component.key !== 'name' ?
                                (component.key.replace(/[0-9]/g, '') !== 'code' && typeof item[component.key] !== "undefined" ? item[component.key][0] : '') :
                                ''
                            )
                    }}

                    <router-link v-if="component.key === 'name'" :to="{ name: 'records.edit', params: { id: $route.params.id, sn: item.sn }}">
                        {{ item[component.key] }}
                    </router-link>

                    <div v-if="component.key.replace(/[0-9]/g, '') === 'code'">
                        <div class="ui small label" v-for="code in item.codes[component.key]">
                            {{ code }}
                        </div>
                    </div>
                </td>
                <td>
                    <time>{{ item.date.created ? moment(item.date.created, "YYYYMMDDHHmmss").format("YYYY-MM-DD HH:mm")
                        : '' }}
                    </time>
                </td>
                <td>
                    <time>{{ item.date.updated ? moment(item.date.updated, "YYYYMMDDHHmmss").format("YYYY-MM-DD HH:mm")
                        : '' }}
                    </time>
                </td>
                <td>
                    <div class="ui fluid mini buttons">
                        <router-link :to="{ name: 'records.edit', params: { id: $route.params.id, sn: item.sn }}"
                                     class="ui positive button" style="padding-left: 0; padding-right: 0">
                            수정
                        </router-link>
                        <button class="ui negative button" @click="deleteItem(item)"
                                style="padding-left: 0; padding-right: 0">
                            삭제
                        </button>
                    </div>
                </td>
            </tr>
            <tr v-show="loading === true">
                <td :colspan="listComponents ? listComponents.length + 5 : 5">
                    <div class="ui active tiny centered inline loader"></div>
                </td>
            </tr>
            <tr v-show="!list.length && loading === false">
                <td :colspan="listComponents ? listComponents.length + 5 : 5">데이터가 없습니다.</td>
            </tr>
            </tbody>
            <tfoot v-show="$parent.busy === false && totalPages">
            <tr>
                <th :colspan="listComponents ? listComponents.length + 5 : 5">
                    <paginate
                            v-model="page"
                            :page-count="totalPages"
                            :page-range="10"
                            :click-handler="paginate"
                            :prev-text="'<i class=\'left chevron icon\'></i>'"
                            :prev-link-class="'icon item'"
                            :next-text="'<i class=\'right chevron icon\'></i>'"
                            :next-link-class="'icon item'"
                            :container-class="'ui right floated pagination menu'"
                            :page-link-class="'item'">
                    </paginate>
                </th>
            </tr>
            </tfoot>
        </table>
        <!-- Data List -->


    </div>
</template>


<script>
    import axios from 'axios'
    import router from '../router'
    import RadioCode from '../components/RadioCode'
    import RadioNumber from '../components/RadioNumber'
    import Checkbox from '../components/Checkbox'
    import SelectCode from '../components/SelectCode'
    import SelectTree from '../components/SelectTree'
    import SelectContainer from '../components/SelectContainer'
    import Input from '../components/Input'
    import Textarea from '../components/Textarea'
    import Header from '../components/Header'
    import Set from '../components/Set'

    export default {
        name: 'List',
        components: {
            RadioCode,
            RadioNumber,
            Checkbox,
            SelectCode,
            SelectTree,
            SelectContainer,
            Input,
            Textarea,
            Header,
            Set
        },
        data() {
            return {
                loading: false,
                name: null,
                searchComponents: null,
                listComponents: null,
                list: [],
                codeList: {},
                searchJson: {
                    'containerSn': parseInt(this.$route.params.id),
                    'confirmed': null,
                    'text': [],
                    'textarea': [],
                    'number': [],
                    'code': [],
                    'container': []
                },
                checked_all: false,
                paging: null,
                totalPages: 0,
                page: 1,
                size: 10,
                total_page: 0,
            };
        },
        mounted() {
            const self = this;

            self.getList(self.$route.params.id);
        },
        watch: {
            '$route.params.id': function (id) {
                this.page = 1;

                this.searchJson = {
                    'containerSn': parseInt(this.$route.params.id),
                    'confirmed': null,
                    'name': null,
                    'text': [],
                    'textarea': [],
                    'number': [],
                    'code': [],
                    'container': []
                };

                this.getList(id);
            }
        },
        methods: {
            getList(id) {
                const self = this;

                self.getSearchComponents(id);
                self.getListComponents(id);
            },
            getSearchComponents(id) {
                const self = this;

                self.searchComponents = [];

                // Call API
                axios.post(process.env.VUE_APP_API_HOST + "/sbculture/rest/container/scheme/search/" + id)
                    .then(res => {
                        self.searchComponents = res.data.container.components;

                        setTimeout(() => {
                            $('.ui.dropdown').dropdown();
                        })
                    })
                    .catch(e => {
                        self.$parent.showError(e, null);
                    });
            },
            getListComponents(id) {
                const self = this;

                self.$parent.busy = true;

                self.listComponents = [];
                self.codeList = {};

                // Call API
                axios.post(process.env.VUE_APP_API_HOST + "/sbculture/rest/container/scheme/list/" + id)
                    .then(res => {
                        self.listComponents = res.data.container.components;
                        self.name = res.data.container.name;

                        self.listComponents.forEach((listComponent) => {
                            if (listComponent.target === 'code') {
                                self.codeList[listComponent.key] = [];
                                self.getCodes(listComponent.key, listComponent.codes, 0, null);
                            }
                        });

                        setTimeout(() => {

                            self.$forceUpdate();

                            self.getData(id);
                        });
                    })
                    .catch(e => {
                        self.$parent.showError(e, null);
                    })
                    .then(() => {
                        self.$parent.busy = false;

                        self.$forceUpdate();
                    });
            },
            getData(id) {
                const self = this;

                self.loading = true;
                self.list = [];

                self.searchJson.containerSn = id;
                self.searchJson.page = self.page;
                self.searchJson.size = self.size;

                // Call API
                axios.post(process.env.VUE_APP_API_HOST + "/sbculture/rest/memex/list", self.searchJson)
                    .then(res => {
                        self.list = res.data.list ? res.data.list : [];
                        self.paging = res.data.paging;
                        self.totalPages = res.data.paging !== undefined ? res.data.paging.totalPages : 0;

                        // Get codes type
                        self.list.forEach((item) => {
                            item.codes = {};

                            for (let codeKey in self.codeList) {
                                item.codes[codeKey] = [];
                            }

                            for (let key in item) {
                                if (key.replace(/[0-9]/g, '') === 'code') {
                                    for (let c = 0; c < item[key][0].length; c++) {
                                        self.codeList[key].find(x => x.sn === parseInt(item[key][0][c])) !== undefined ?
                                        item.codes[key].push(self.codeList[key].find(x => x.sn === parseInt(item[key][0][c])).name) :
                                        '';
                                    }
                                }
                            }
                        });
                    })
                    .catch(e => {
                        self.$parent.showError(e, null);
                    })
                    .then(() => {
                        self.loading = false;

                        self.$forceUpdate();

                        self.searchJson = {
                            'containerSn': parseInt(this.$route.params.id),
                            'confirmed': null,
                            'name': null,
                            'text': [],
                            'textarea': [],
                            'number': [],
                            'code': [],
                            'container': []
                        };
                    });
            },
            getCodes(key, codes, depth, parentSn) {
                const self = this;

                if (codes === undefined) return;

                for (let i = 0; i < codes.length; i++) {
                    codes[i].depth = depth;

                    self.codeList[key].push(codes[i]);

                    if (codes[i].codes) {
                        self.getCodes(key, codes[i].codes, depth + 1, codes[i].sn);
                    }
                }
            },
            search() {
                const self = this;

                self.page = 1;

                // Parse JSON
                self.searchComponents.forEach((component) => {
                    // Confirmed
                    if (component.definition === 'radio' && component.type === 'code' && component.key === 'confirm' && Array.from(document.getElementsByName('field' + component.sn + '_0')).find(r => r.checked) !== undefined) {
                        self.searchJson.confirmed = parseInt(Array.from(document.getElementsByName('field' + component.sn + '_0')).find(r => r.checked).value);
                    }
                    // Name
                    else if (component.key === 'name' && document.querySelector('[data-key="name"]')) {
                        self.searchJson.name = document.querySelector('[data-key="name"]').value;
                    }
                    // Set
                    else if (component.definition === 'set') {
                        component.components.forEach((setComponent, idx) => {
                            self.pushToJson(setComponent, true);
                        });
                    }
                    // Everything else
                    else {
                        self.pushToJson(component, false);
                    }
                });

                self.getData(this.$route.params.id);
            },
            reset() {
                const self = this;

                self.searchJson = {
                    'containerSn': parseInt(this.$route.params.id),
                    'confirmed': null,
                    'name': null,
                    'text': [],
                    'textarea': [],
                    'number': [],
                    'code': [],
                    'container': []
                };

                self.getList(this.$route.params.id);
            },
            pushToJson(component) {
                const self = this;
                let obj = {};
                let len = document.querySelectorAll('[name="field' + component.sn + '"]').length;

                for (let index = 0; index < len; index++) {
                    // Tree code
                    if (component.definition === 'select' && component.type === 'tree') {

                        for (let d = 1; d <= component.option.depth; d++) {
                            obj = {
                                "id": {
                                    "key": component.key,
                                    "index": index,
                                    "no": d
                                },
                                "value": document.getElementById('field' + component.sn + '_' + index + '_' + d).value
                            };

                            if(obj.value && obj.value !== '-1') {
                                self.searchJson.code.push(obj);
                            }
                        }

                    } else {

                        obj = {
                            "id": {
                                "key": component.key,
                                "index": index
                            },
                            "value": component.definition === 'radio' ?
                                (document.querySelector('input[name="field' + component.sn + '_' + index + '"]:checked') ? document.querySelector('input[name="field' + component.sn + '_' + index + '"]:checked').value : null) :
                                document.getElementById('field' + component.sn + '_' + index).value
                        };

                        // Code
                        if ((component.definition === 'radio' && component.type === 'code') || (component.definition === 'select' && component.type === 'code')) {
                            obj.id.no = 1;
                        }

                        if (component.target === 'text' && obj.value) {
                            self.searchJson.text.push(obj);
                        } else if (component.target === 'textarea' && obj.value) {
                            self.searchJson.textarea.push(obj);
                        } else if (component.target === 'number' && obj.value) {
                            self.searchJson.number.push(obj);
                        } else if (component.target === 'code' && obj.value && obj.value !== '-1') {
                            self.searchJson.code.push(obj);
                        }
                    }
                }
            },
            paginate(page) {
                const self = this;

                self.page = page;

                self.getData(this.$route.params.id);
            },
            toggleCheckAll() {
                const self = this;

                for (let i in self.list) {
                    self.list[i].is_checked = self.checked_all;
                }
            },
            toggleCheck(item) {
                const self = this;

                item.is_checked = item.is_checked === undefined ? true : item.is_checked;

                const len = self.list.filter(function (x) {
                    return x.is_checked === true;
                }).length;

                self.checked_all = (len === self.page_size);
            },
            downloadExcel() {

            },
            downloadAllExcel() {

            },
            deleteItem(item) {
                const self = this;

                if (!confirm('정말로 삭제할까요?')) return false;

                // Call API
                axios.post(process.env.VUE_APP_API_HOST + '/sbculture/rest/memex/delete/' + item.sn)
                    .then(res => {

                        self.$parent.showSuccess('삭제되었습니다.', null);

                        self.getData(parseInt(this.$route.params.id))
                    })
                    .catch(e => {
                        self.$parent.showError(e, null);
                    });
            }
        }
    }
</script>