<template>
    <div class="field" v-bind:class="{ required: props.option && props.option.required }">
        <label>{{ props.label }}</label>
        <div class="inline fields">
            <div class="field" v-for="n in numbers">
                <div class="ui radio checkbox">
                    <input type="radio"
                           :name="'field'+props.sn+'_'+index"
                           :id="'field'+props.sn+'_'+index+'_'+n"
                           class="hidden"
                           v-model="data"
                           :value="n"
                           :data-key="props.key"
                           :data-index="index"
                    >
                    <label :for="'field'+props.sn+'_'+index+'_'+n">{{ n }}</label>
                </div>
            </div>
        </div>
        <p class="postscript" v-if="props.postscript">{{ props.postscript }}</p>
        <input
            :name="'field'+props.sn"
            v-model="data"
            style="opacity: 0; visibility: hidden;height: 0; margin0; padding: 0;"
        />
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: 'RadioNumber',
        props: [
            'props',
            'index',
        ],
        data: function () {
            return {
                numbers: [],
                data: null
            }
        },
        computed: {
            rangeNumbers: function () {
                return this.numbers.filter(function (number) {
                return number % 2 === 0
            })
            }
        },
        mounted() {
            const self = this;

            for(let i = parseInt(self.props.option.start); i <= parseInt(self.props.option.end); i++) {
                self.numbers.push(i);
            }

            $('.ui.radio.checkbox').checkbox();
        }
    }
</script>