<template>
    <div class="ui content content-anchor">
        <form class="ui form" name="record-form" id="record-form" v-on:submit.prevent="create">
            <div class="ui toolbar">
                <h1 id="toolbar-title" v-show="name">성북구의 {{ name }} - 자료 {{ mode === 'create' ? '등록' : '수정' }}</h1>
                <div class="toolbar-btns">
                    <div class="ui buttons">
                        <router-link :to="{ name: 'records.list', params: { id: $route.params.id }}" class="ui button">
                            취소
                        </router-link>
                        <button class="ui button" type="button" @click="deleteItem" v-show="mode === 'edit'">삭제</button>
                    </div>
                    <button class="primary ui button">저장</button>
                </div>
            </div>
            <div class="ui grid">
                <div :class="component.option && component.option.depth ? getGrid(component.grid * parseInt(component.option.depth)) + ' wide column' : getGrid(component.grid) + ' wide column'"
                     v-for="(component, index) in components"
                >
                    <Header
                            v-if="component.definition === 'header'"
                            v-bind:props="component"
                            v-bind:index="0"
                    ></Header>
                    <Input
                            v-if="component.definition === 'input'"
                            v-bind:props="component"
                            v-bind:data="component.data"
                            v-bind:index="0"
                    ></Input>
                    <Textarea
                            v-if="component.definition === 'textarea'"
                            v-bind:props="component"
                            v-bind:data="component.data"
                            v-bind:index="0"
                    ></Textarea>
                    <Checkbox
                            v-if="component.definition === 'checkbox'"
                            v-bind:props="component"
                            v-bind:data="component.data"
                            v-bind:index="0"
                    ></Checkbox>
                    <RadioCode
                            v-if="component.definition === 'radio' && component.type === 'code'"
                            v-bind:props="component"
                            v-bind:data="component.data"
                            v-bind:index="0"
                    ></RadioCode>
                    <RadioNumber
                            v-if="component.definition === 'radio' && component.type === 'number'"
                            v-bind:props="component"
                            v-bind:data="component.data"
                            v-bind:index="0"
                    ></RadioNumber>
                    <SelectCode
                            v-if="component.definition === 'select' && component.type === 'code'"
                            v-bind:props="component"
                            v-bind:data="component.data"
                            v-bind:index="0"
                    ></SelectCode>
                    <SelectTree
                            v-if="component.definition === 'select' && component.type === 'tree'"
                            v-bind:props="component"
                            v-bind:data="component.data"
                            v-bind:index="0"
                    ></SelectTree>
                    <SelectContainer
                            v-if="component.definition === 'select' && component.type === 'container'"
                            v-bind:props="component"
                            v-bind:data="component.data"
                            v-bind:index="0"
                    ></SelectContainer>
                    <Set
                            v-if="component.definition === 'set'"
                            v-bind:props="component"
                    ></Set>
                    <File
                        v-if="component.definition === 'file'"
                        v-bind:props="component"
                        v-bind:data="component.data"
                        v-bind:index="0"
                        v-bind:sn="parseInt($route.params.id)"
                ></File>
                </div>



                </File>
            </div>

            <div class="ui error message"></div>

        </form>


        <div class="ui section">
            <a
                    v-for="(component, index) in components"
                    v-if="component.definition === 'header' && component.option.anchor"
                    :class="'section-title section-title-'+component.option.level"
                    @click="moveScroll('#field'+component.sn)"
            >
                {{ component.label }}
            </a>
        </div>
    </div>
</template>


<script>
    import axios from 'axios'
    import router from '../router'
    import RadioCode from '../components/RadioCode'
    import RadioNumber from '../components/RadioNumber'
    import Checkbox from '../components/Checkbox'
    import SelectCode from '../components/SelectCode'
    import SelectTree from '../components/SelectTree'
    import SelectContainer from '../components/SelectContainer'
    import Input from '../components/Input'
    import Textarea from '../components/Textarea'
    import Header from '../components/Header'
    import Set from '../components/Set'
    import File from '../components/File'

    export default {
        name: 'Form',
        components: {
            RadioCode,
            RadioNumber,
            Checkbox,
            SelectCode,
            SelectTree,
            SelectContainer,
            Input,
            Textarea,
            Header,
            Set,
            File
        },
        data() {
            return {
                mode: null,
                name: null,
                components: null,
                items: null,
                saveJson: {
                    'containerSn': parseInt(this.$route.params.id),
                    'confirmed': null,
                    'text': [],
                    'textarea': [],
                    'number': [],
                    'code': [],
                    'container': [],
                    'image': []
                }
            };
        },
        mounted() {
            const self = this;

            self.getForm();

            // Set mode
            self.mode = self.$route.name === 'records.edit' ? 'edit' : 'create';

        },
        watch: {
            '$route.params.id': function (id) {
                this.getForm();

                // Remove old file array
                $('.file-array').remove();
            }
        },
        methods: {
            getForm() {
                const self = this;
                self.$parent.busy = true;

                self.components = [];

                // Call API
                axios.post(process.env.VUE_APP_API_HOST + "/sbculture/rest/container/scheme/view/" + self.$route.params.id)
                    .then(res => {
                        self.components = res.data.container.components;
                        self.name = res.data.container.name;

                        // Validation
                        setTimeout(() => {
                            self.validate();
                        });

                        if (self.mode === 'edit') {
                            self.getData();
                        } else {
                            self.$parent.busy = false;
                        }
                    })
                    .catch(e => {
                        self.$parent.showError(e, null);
                    })
                    .then(() => {

                    });
            },
            getData() {
                const self = this;

                // Call API
                axios.post(process.env.VUE_APP_API_HOST + "/sbculture/rest/memex/view/" + self.$route.params.sn)
                    .then(res => {
                        self.items = res.data.memex;
                        self.$parent.busy = false;

                        for (let key in self.items) {
                            const type = key.replace(/[0-9]/g, '');

                            // Add set slots
                            if (typeof self.items[key] === 'object' && self.items[key].length !== undefined && self.items[key].length > 1) {

                                if(!$('[data-key="' + key + '"]').closest('.ui.set-sort').attr('added')) {
                                    for (let s = 1; s < self.items[key].length; s++) {
                                        $('[data-key="' + key + '"]').closest('.ui.set-sort').find('.button-add').trigger('click');
                                    }
                                    $('[data-key="' + key + '"]').closest('.ui.set-sort').attr('added', 1);
                                }
                            }

                            // Fill in
                            setTimeout(() => {
                                // Confirm
                                if ((type === 'confirm') && document.querySelector('[data-key="confirm"][value="' + self.items[key] + '"]')) {
                                    document.querySelector('[data-key="confirm"][value="' + self.items[key] + '"]').checked = true;
                                }
                                // Parent Sn
                                if ((type === 'parentSn') && document.querySelector('select[data-key="parentSn"]')) {
                                    let parentSn = document.querySelector('select[data-key="parentSn"]');
                                    parentSn.options[parentSn.options.length] = new Option(self.items[key].name, self.items[key].sn, true, true);
                                }
                                // Name
                                else if ((type === 'name') && document.querySelector('[data-key="name"]')) {
                                    document.querySelector('[data-key="name"]').value = self.items[key];
                                }
                                // Radio button
                                else if (
                                    (type === 'number' || (type === 'code' && document.querySelector('[data-key="' + key + '"]').getAttribute('type') === 'radio')) &&
                                    document.querySelector('[data-key="' + key + '"]')
                                ) {
                                    for (let s = 0; s < self.items[key].length; s++) {
                                        if(self.items[key][s] && document.querySelector('[data-key="' + key + '"][data-index="' + s + '"][value="' + self.items[key][s] + '"]')) {
                                            document.querySelector('[data-key="' + key + '"][data-index="' + s + '"][value="' + self.items[key][s] + '"]').checked = true;
                                        }
                                    }
                                }
                                // Container
                                else if ((type === 'container')) {

                                    for (let i = 0; i < self.items[key].length; i++) {
                                        let container = document.querySelectorAll('select[data-key="' + key + '"]')[i];

                                        for (let t = 0; t < self.items[key][i].length; t++) {
                                            container.options[container.options.length] = new Option(self.items[key][i][t].name, self.items[key][i][t].sn, true, true);
                                        }
                                    }
                                }
                                // Image
                                else if ((type === 'image')) {
                                    for (let i = 0; i < self.items[key].length; i++) {
                                        for (let t = 0; t < self.items[key][i].length; t++) {
                                            self.$bus.$emit('add_file', self.items[key][i][t], 'thumb1');
                                        }
                                    }
                                }
                                // Code
                                else if ((type === 'code')) {
                                    for (let i = 0; i < self.items[key].length; i++) {
                                        let code = document.querySelectorAll('select[data-key="' + key + '"]')[i];

                                        // Tree
                                        if(code.getAttribute('data-select') === 'tree') {
                                            for (let t = 1; t <= self.items[key][i].length; t++) {
                                                setTimeout(() => {
                                                    const tree = document.querySelectorAll('[data-key="' + key + '"][data-depth="' + t + '"]')[i];
                                                    tree.value = self.items[key][i][t - 1];

                                                    self.$bus.$emit('change_tree_'+tree.getAttribute('data-sn')+'_'+(t - 1), self.items[key][i][t - 1]);
                                                }, 100)
                                            }
                                        }
                                        // Code single
                                        else if(code.getAttribute('data-select') === 'code' && !code.multiple) {
                                            document.querySelectorAll('[data-key="' + key + '"]')[i].value = self.items[key][i];

                                            $('.ui.dropdown.code').dropdown();
                                        }
                                        // Code multiple
                                        else if(code.getAttribute('data-select') === 'code' && code.multiple) {
                                            for (let o = 0; o < code.options.length; o++) {
                                                code.options[o].selected = self.items[key][i].indexOf(code.options[o].value) >= 0;
                                            }

                                            $('.ui.dropdown.code').dropdown();
                                        }
                                    }
                                }
                                else if (document.querySelectorAll('[data-key="' + key + '"]').length) {
                                    for (let i = 0; i < document.querySelectorAll('[data-key="' + key + '"]').length; i++) {

                                        // Default
                                        document.querySelectorAll('[data-key="' + key + '"]')[i].value = self.items[key][i];
                                    }
                                }
                            })
                        }

                        //$('.ui.dropdown').dropdown();
                    })
                    .catch(e => {
                        self.$parent.showError(e, null);
                    })
                    .then(() => {

                    });
            },
            moveScroll(id) {
                $('html, body').stop(true, true).animate({
                    scrollTop: $(id).offset().top - 80 - 16
                }, 1000);
            },
            validate() {
                const self = this;

                $('.ui.form').form();

                self.components.forEach((component) => {
                    if (component.option && component.option.required === true) {

                        if (component.definition === 'radio' || component.definition === 'checkbox') {
                            $('.ui.form').form('add rule', 'field' + component.sn + '_' + 0, ['checked'])
                        } else if (component.definition === 'select' && component.type === 'tree') {
                            $('.ui.form').form('add rule', 'field' + component.sn + '_' + component.option.depth, ['empty', 'not[-1]'])
                        } else {
                            $('.ui.form').form('add rule', 'field' + component.sn, ['empty'])
                        }
                    }
                })
            },
            create() {
                const self = this;

                // Validation check
                if (!$('.ui.form').form('is valid') || self.$parent.busy) return false;

                self.$parent.busy = true;

                self.createItem();
            },
            createItem() {
                const self = this;
                //self.$parent.busy = true;

                // Parse JSON
                self.components.forEach((component) => {
                    // Confirmed
                    if (component.definition === 'radio' && component.type === 'code' && component.key === 'confirm') {
                        //self.saveJson.confirmed = parseInt(Array.from(document.getElementsByName('field' + component.sn + '_0')).find(r => r.checked).value);
                        self.saveJson.confirmed = parseInt(Array.from(document.getElementsByName('field' + component.sn + '_0')).find(r => r.checked).value);
                    }
                    // Parent Sn
                    else if (component.target === 'memex' && component.type === 'container' && component.key === 'parentSn') {
                        self.saveJson.parentSn = parseInt(document.querySelector('[data-key="parentSn"]').value);
                    }
                    // Name
                    else if (component.target === 'memex' && component.type === 'text' && component.key === 'name') {
                        self.saveJson.name = document.querySelector('[data-key="name"]').value;
                    }
                    // Set
                    else if (component.definition === 'set') {
                        component.components.forEach((setComponent, idx) => {
                            self.pushToJson(setComponent, true);
                        });
                    }
                    // Everything else
                    else {
                        self.pushToJson(component, false);
                    }
                });

                const api = self.mode === 'create' ? '/sbculture/rest/memex/save' : '/sbculture/rest/memex/update/' + this.$route.params.sn;

                // Call API
                axios.post(process.env.VUE_APP_API_HOST + api, self.saveJson)
                    .then(res => {
                        self.saveJson = {
                            'containerSn': parseInt(this.$route.params.id),
                            'confirmed': null,
                            'text': [],
                            'textarea': [],
                            'number': [],
                            'code': [],
                            'container': [],
                            'image': []
                        };

                        // Redirect
                        router.push({name: 'records.list', id: this.$route.params.id})
                    })
                    .catch(e => {
                        self.$parent.showError(e, null);
                    })
                    .then(() => {
                        self.$parent.busy = false;
                    });
            },
            pushToJson(component, multiple) {
                const self = this;
                let obj = {};
                let len = document.querySelectorAll('[name="field' + component.sn + '"]').length;

                for (let index = 0; index < len; index++) {
                    // Tree code
                    if (component.definition === 'select' && component.type === 'tree') {

                        for (let d = 1; d <= component.option.depth; d++) {
                            obj = {
                                "id": {
                                    "key": component.key,
                                    "index": index,
                                    "no": d
                                },
                                "value": document.getElementById('field' + component.sn + '_' + index + '_' + d).value
                            };

                            self.saveJson.code.push(obj);
                        }

                    }
                    // Container
                    else if (component.target === 'container' && component.type === 'container') {
                        let containers = document.querySelectorAll('#field' + component.sn + '_' + index + ' option:checked');
                        for(let c = 0; c < containers.length ; c++) {
                            obj = {
                                "id": {
                                    "key": component.key,
                                    "index": index,
                                    "no": c + 1
                                },
                                "value": containers[c].value
                            };

                            self.saveJson.container.push(obj);
                        }
                    }
                    // Image
                    else if (component.target === 'image' && component.type === 'image') {
                        let files = document.querySelectorAll('.file' + component.sn + '_' + index);
                        for(let f = 0; f < files.length ; f++) {
                            obj = {
                                "id": {
                                    "key": component.key,
                                    "index": index,
                                    "no": f + 1
                                },
                                "value": files[f].value
                            };

                            self.saveJson.image.push(obj);
                        }
                    }
                    // Code multiple
                    else if (component.definition === 'select' && component.type === 'code' && (component.option && component.option.multiple)) {
                        let codes = document.querySelectorAll('#field' + component.sn + '_' + index + ' option:checked');
                        for(let c = 0; c < codes.length ; c++) {
                            obj = {
                                "id": {
                                    "key": component.key,
                                    "index": index,
                                    "no": c + 1
                                },
                                "value": codes[c].value
                            };

                            self.saveJson.code.push(obj);
                        }
                    }
                    else {

                        obj = {
                            "id": {
                                "key": component.key,
                                "index": index
                            },
                            "value": component.definition === 'radio' ?
                                (document.querySelector('input[name="field' + component.sn + '_' + index + '"]:checked') ? document.querySelector('input[name="field' + component.sn + '_' + index + '"]:checked').value : null) :
                                document.getElementById('field' + component.sn + '_' + index).value
                        };

                        // Code single
                        if ((component.definition === 'radio' && component.type === 'code') || (component.definition === 'select' && component.type === 'code')) {
                            obj.id.no = 1;
                        }

                        if (component.target === 'text') {
                            self.saveJson.text.push(obj);
                        } else if (component.target === 'textarea') {
                            self.saveJson.textarea.push(obj);
                        } else if (component.target === 'number') {
                            self.saveJson.number.push(obj);
                        } else if (component.target === 'code') {
                            self.saveJson.code.push(obj);
                        }
                    }
                }
            },
            deleteItem() {
                const self = this;

                if(!confirm('정말로 삭제할까요?')) return false;

                // Call API
                axios.post(process.env.VUE_APP_API_HOST + '/sbculture/rest/memex/delete/'+self.$route.params.sn)
                    .then(res => {

                        self.$parent.showSuccess('삭제되었습니다.', null);

                        // Redirect
                        router.push({name: 'records.list', id: this.$route.params.id})
                    })
                    .catch(e => {
                        self.$parent.showError(e, null);
                    });
            }
        }
    }
</script>