<template>
    <div class="login">
        <div class="ui middle aligned center aligned grid">
            <div class="column login-column">
                <h2 class="ui image header">
                    <div class="content">
                        성북구 마을기록 아카이브 관리 시스템
                    </div>
                </h2>
                <p>로그인을 해주세요.</p>

                <sui-message
                        v-show="msg"
                        header="로그인 실패"
                        :content="msg"
                        dismissable
                        @dismiss="handleDismiss"
                        negative
                />

                <form class="ui large form" method="POST" v-on:submit.prevent="login">
                    <div class="ui segment segment-login">
                        <div class="field">
                            <div class="ui left icon input">
                                <i class="user icon"></i>
                                <input type="text" name="email" placeholder="아이디" v-model="username"/>
                            </div>
                        </div>
                        <div class="field">
                            <div class="ui left icon input">
                                <i class="lock icon"></i>
                                <input type="password" name="password" placeholder="비밀번호" v-model="password"/>
                            </div>
                        </div>
                        <router-link class="ui fluid large primary submit button" :to="{ name: 'records.list', params: { id: 1 }}">로그인</router-link>
                        <!--<button type="submit" class="ui fluid large primary submit button">로그인</button>-->
                    </div>
                    <div class="ui error message"></div>
                </form>
            </div>
        </div>
        <div class="logo"></div>
    </div>
</template>


<style>
    .login {
        margin-top: 60px;
    }
    .login-column {
        max-width: 450px;
    }
    .ui.negative.message {
        text-align: left;
    }
</style>


<script>
    import axios from 'axios'
    import router from '../router'

    export default {
        name: 'login',
        components: {

        },
        data() {
            return {
                username: null,
                password: null,
                msg: null
            };
        },
        mounted() {

        },
        methods: {
            login() {
                const self = this;

                let formData = new FormData();

                // Set data
                formData.append('username', self.username);
                formData.append('password', self.password);

                // Call API
                axios.post(process.env.VUE_APP_HOST+'/login', formData,
                    {
                        headers: {
                            'Content-Type': 'multipart/form-data'
                        }
                    })
                    .then(response => {

                    })
                    .catch(e => {
                        self.$parent.showError(e, null);
                        self.msg = '아이디와 비밀번호를 확인해주세요.';
                    })
                    .then(() => {
                        axios.get(process.env.VUE_APP_API_HOST+'/auth').then(response => {
                            router.push({ name: 'contents.business.index' })
                        })
                            .catch(e => {
                                self.msg = '아이디와 비밀번호를 확인해주세요.';
                            });
                    })
            },
            handleDismiss() {
                this.msg = null;
            }
        }
    }
</script>